/* eslint-env browser */
/* eslint space-before-keywords: 0*/
/* eslint space-after-keywords: 0*/
/* global $ */

(function() {
  'use strict';
  // Check to make sure service workers are supported in the current browser,
  // and that the current page is accessed from a secure origin. Using a
  // service worker from an insecure origin will trigger JS console errors. See
  // http://www.chromium.org/Home/chromium-security/prefer-secure-origins-for-powerful-new-features
  var isLocalhost = Boolean(window.location.hostname === 'localhost' ||
    // [::1] is the IPv6 localhost address.
    window.location.hostname === '[::1]' ||
    // 127.0.0.1/8 is considered localhost for IPv4.
    window.location.hostname.match(
      /^127(?:\.(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)){3}$/
    )
  );

  if ('serviceWorker' in navigator &&
    (window.location.protocol === 'https:' || isLocalhost)) {
    navigator.serviceWorker.register('service-worker.js')
      .then(function(registration) {
        // updatefound is fired if service-worker.js changes.
        registration.onupdatefound = function() {
          // updatefound is also fired the very first time the SW is installed,
          // and there's no need to prompt for a reload at that point.
          // So check here to see if the page is already controlled,
          // i.e. whether there's an existing service worker.
          if (navigator.serviceWorker.controller) {
            // The updatefound event implies that registration.installing is set:
            // https://slightlyoff.github.io/ServiceWorker/spec/service_worker/index.html#service-worker-container-updatefound-event
            var installingWorker = registration.installing;

            installingWorker.onstatechange = function() {
              switch (installingWorker.state) {
                case 'installed':
                  // At this point, the old content will have been purged and the
                  // fresh content will have been added to the cache.
                  // It's the perfect time to display a "New content is
                  // available; please refresh." message in the page's interface.
                  break;

                case 'redundant':
                  throw new Error('The installing ' +
                    'service worker became redundant.');

                default:
                  // Ignore
              }
            };
          }
        };
      }).catch(function(e) {
        console.error('Error during service worker registration:', e);
      });
  }

  // Your custom JavaScript goes here

  var $mainSection = $('.main-section');
  var ACTIVE_CLASS = 'inner_panel--active';
  var notUpdated = false;
  var revContain = $('#revenue-chart');

  var sectionSlideIn = {

    init: function() {
      $(window).on('scroll', function() {
        var top = $(window).scrollTop();
        sectionSlideIn.bindSections(top);
      });
    },

    checkDist: function(obj, top) {
      if (top >= $(obj).offset().top - $(window).height() * 0.35) {
        $(obj).addClass('in-view');
      } else {
        $(obj).removeClass('in-view');
      }
      var offset = $('.tabs-section').offset().top;

      if (top >= offset - $(window).height() * 0.2 && notUpdated === false) {
        //  buildCharts();
        $('.tab-one').addClass('in-view');
        buildCharts();
        notUpdated = true;
      }
    },

    bindSections: function(top) {
      var _ = this;
      $mainSection.each(function() {
        var $section = $(this);
        _.checkDist($section, top);
      });
    }

  };

  // init section slider-ins
  sectionSlideIn.init();

  var toggleSections = {

    init: function() {
      var _ = this;
      $mainSection.on('click', '.toggle-active', function(e) {
        var $section = $(this).closest($mainSection);
        e.preventDefault();
        e.stopPropagation();
        _.swapState($section);
        if ($(window).width() < 600) {
          $(window).scrollTop($section.offset().top);
        }
      });
    },

    swapState: function($section) {
      var active = $section.find(`.${ACTIVE_CLASS}`);
      var inactive = $section.find(`.inner_panel:not(.${ACTIVE_CLASS})`);
      active.removeClass(ACTIVE_CLASS);
      inactive.addClass(ACTIVE_CLASS);
    }

  };

  toggleSections.init();

  var nextButton = function nextButton() {
    $('.next-button').on('click', function(e) {
      e.preventDefault();
      e.stopPropagation();
      var ele = $(this).attr('href');
      var nextTop = $(ele).offset().top;
      $('html, body').animate({
        scrollTop: nextTop
      });
    });
  };

  nextButton();
  // tabs
  var tabs = {
    init: function() {
      var tabs = $('.tabsList').find('a');
      var _ = this;
      tabs.on('click', function(e) {
        e.preventDefault();
        tabs.removeClass('active');

        var anchor = $(this).attr('href').split('#')[1];

        $(this).addClass('active');

        _.clickHandler(anchor);
      });
    },
    clickHandler: function($tab) {
      $('.tabPanel').removeClass('active');
      $('.tabWrapper').find('.tabPanel').eq($tab - 1).addClass('active');

      if ($($tab).eq(0)) {
        buildCharts();
      }
    }
  };

  tabs.init();

  var offCanvas = {
    init: function() {
      $('.mainNav_trigger').on('click', function() {
        $('body').toggleClass('off-canvas-active');
      });
      $('.off-canvas').on('click', 'a', function() {
        $('body').removeClass('off-canvas-active');
      });
    }
  };

  offCanvas.init();

  $('.read-more').on('click', function(e) {
    e.preventDefault();
    e.stopPropagation();
    $(this).parent().toggleClass('active');
    if ($(this).text() === 'View More Donors +') {
      $(this).text('View Less');
    } else {
      $(this).text('View More Donors +');
      $(window).scrollTop($('.tabsList').offset().top);
    }
  });

  var revenueData = {
    series: [{
      value: 60,
      name: 'Series 1',
      meta: 'Meta One'
    }, {
      value: 26,
      name: 'Series 2',
      meta: 'Meta Two'
    }, {
      value: 8,
      name: 'Series 3',
      meta: 'Meta Three'
    }, {
      value: 6,
      name: 'Series 4',
      meta: 'Meta Three'
    }]
  };

  var expenseData = {
    series: [{
      value: 79,
      name: 'Series 1',
      meta: 'Meta One'
    }, {
      value: 11,
      name: 'Series 2',
      meta: 'Meta Two'
    }, {
      value: 8,
      name: 'Series 3',
      meta: 'Meta Three'
    }, {
      value: 2,
      name: 'Series 4',
      meta: 'Meta Three'
    }]
  };

  function buildCharts() {
    var chart1 = new Chartist.Pie('#revenue-chart', revenueData, {
      donut: true,
      donutWidth: 100,
      startAngle: 0,
      labelInterpolationFnc: function(value) {
        return value + '%';
      }
    });

    var chart2 = new Chartist.Pie('#revenue-chart-2', expenseData, {
      donut: true,
      donutWidth: 100,
      startAngle: 0,
      labelInterpolationFnc: function(value) {
        if (value === 2) {
          return null;
        }
        return value + '%';
      }
    });
  }

  buildCharts();

  $('.back-to-top').on('click', function(e) {
    e.preventDefault();
    $('html, body').animate({
      scrollTop: 0
    }, 500);
  });
  $('.down-arrow').on('click', function(e) {
    e.preventDefault();
    var height = $('.ceo-panel').offset().top;
    $('html, body').animate({
      scrollTop: height
    }, 500);
  });

  $('.button-alt').on('click', function(e) {
      e.preventDefault();
      var src = $(this).attr('href');
      $('.lightbox-container')
        .addClass('active').empty().prepend('<iframe allowfullscreen src=' + src + '?autoplay=1'+' />');
  });
  $('.lightbox-container').on('click', function(e) {
    if (e.target !== 'iframe') {
      $('.lightbox-container').empty().removeClass('active');
    }
  });
})();
